import React, { useState, useEffect } from 'react';
import goldCoinIcon from '../assets/icons/gold-coin.png';
import './styles/TradeHistory.css';
import { fetchTradeHistory } from '../services/googleSheetsService';
import { TradeHistoryShimmer } from '../components/ui/ShimmerLoading';
import LoadingSpinner from './LoadingSpinner';

function TradeHistory({ isDashboard, data }) {
  const [tradeHistory, setTradeHistory] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (data) {
      setTradeHistory(data);
      setIsLoading(false);
    } else {
      const loadData = async () => {
        try {
          const token = localStorage.getItem('token');
          if (!token) {
            throw new Error('No authentication token found');
          }
          
          const fetchedData = await fetchTradeHistory(token);
          setTradeHistory(fetchedData);
          setError(null);
          setIsLoading(false);
        } catch (e) {
          console.error("Error loading trade history:", e);
          setError(e.message || 'Failed to load trade history');
          setIsLoading(false);
        }
      };

      loadData();
      const intervalId = setInterval(loadData, 5000);
      return () => clearInterval(intervalId);
    }
  }, [data]);

  const formatDate = (date) => {
    if (!date) return "-";
    try {
      // Handle both date string and serial number formats
      let dateObj;
      if (typeof date === 'number') {
        // Convert Excel serial date to JS Date
        dateObj = new Date((date - 25569) * 86400 * 1000);
      } else {
        dateObj = new Date(date);
      }
      
      if (isNaN(dateObj.getTime())) {
        return "-";
      }
      
      return dateObj.toLocaleDateString('en-GB', { 
        day: '2-digit', 
        month: '2-digit',
        year: 'numeric'
      });
    } catch (error) {
      console.error('Error formatting date:', error);
      return "-";
    }
  };

  const formatPNL = (pnl) => {
    if (!pnl || isNaN(parseFloat(pnl))) return "-";
    const value = parseFloat(pnl);
    return `${value >= 0 ? '+' : ''}${value.toFixed(2)}%`;
  };

  const displayedHistory = isDashboard ? tradeHistory.slice(0, 6) : tradeHistory;

  if (!tradeHistory && !error) {
    return <TradeHistoryShimmer />;
  }

  return (
    <div className="trade-history">
      <table className="trade-history-table">
        <tbody>
          {displayedHistory.map((trade, index) => (
            <tr key={index}>
              <td className="date-column">
                {formatDate(trade.timestamp)}
              </td>
              <td className="name-column">
                <div className="currency-info">
                  <img src={goldCoinIcon} alt="Gold Coin" className="currency-icon" />
                  <span className="currency-name">{trade.symbol}</span>
                  <span className={`trade-type ${trade.direction?.toLowerCase()}`}>
                    {trade.direction}
                  </span>
                </div>
              </td>
              <td className={`pnl-column ${parseFloat(trade.pnl) >= 0 ? 'positive' : 'negative'}`}>
                {formatPNL(trade.pnl)}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default TradeHistory;
