import config from '../config/config';
import { getAuth, signInWithPopup, GoogleAuthProvider } from 'firebase/auth';

export const loginUser = async (credentials) => {
  try {
    const response = await fetch(`${config.API_URL}/api/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(credentials),
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.message || 'Failed to login');
    }

    return await response.json();
  } catch (error) {
    console.error('Login error:', error);
    throw error;
  }
};

export const loginWithGoogle = async () => {
  try {
    const auth = getAuth();
    const provider = new GoogleAuthProvider();
    
    // Add these lines to force account selection
    provider.addScope('email');
    provider.setCustomParameters({
      prompt: 'select_account'
    });
    
    const result = await signInWithPopup(auth, provider);
    const idToken = await result.user.getIdToken();

    const response = await fetch(`${config.API_URL}/api/login/google`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ idToken }),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.error || 'Failed to login with Google');
    }

    const data = await response.json();
    localStorage.setItem('token', data.token);
    return data;
  } catch (error) {
    console.error('Google login error:', error);
    throw new Error('Failed to login with Google. Please try again.');
  }
};

export const API_URL = config.API_URL;

const handleResponse = async (response) => {
  if (!response.ok) {
    const errorText = await response.text();
    console.error('Server response:', errorText);
    throw new Error(`HTTP error! status: ${response.status}`);
  }
  
  const contentType = response.headers.get('content-type');
  if (contentType && contentType.includes('application/json')) {
    return response.json();
  }
  throw new Error('Invalid response format: Expected JSON');
};

const getValidToken = async () => {
  const token = localStorage.getItem('token');
  if (!token) throw new Error('No authentication token found');
  
  try {
    // Verify token is still valid
    const response = await fetch(`${config.API_URL}/api/verify-token`, {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });
    
    if (!response.ok) {
      // Token invalid, try to refresh
      const auth = getAuth();
      const user = auth.currentUser;
      if (user) {
        const newToken = await user.getIdToken(true);
        localStorage.setItem('token', newToken);
        return newToken;
      }
      throw new Error('Unable to refresh token');
    }
    
    return token;
  } catch (error) {
    console.error('Token validation error:', error);
    throw error;
  }
};

let performanceDataCache = null;
let lastFetchTime = 0;
const CACHE_DURATION = 30000; // 30 seconds

export const fetchDashboardData = async () => {
  const now = Date.now();
  
  // Return cached data if it's still fresh
  if (performanceDataCache && (now - lastFetchTime) < CACHE_DURATION) {
    return performanceDataCache;
  }

  try {
    const token = await getValidToken();
    const fetchConfig = {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      cache: 'no-cache'
    };

    const response = await fetch(`${config.API_URL}/api/performance-data`, fetchConfig);
    const data = await handleResponse(response);
    
    // Update cache
    performanceDataCache = data;
    lastFetchTime = now;
    
    return data;
  } catch (error) {
    console.error('Dashboard data fetch error:', error);
    if (performanceDataCache) {
      return performanceDataCache;
    }
    throw error;
  }
};

