import React from 'react';

export const PerformanceCardShimmer = () => {
  return (
    <div className="performance-card shimmer-card">
      <div className="card-header">
        <div className="card-icon shimmer" />
        <div className="card-title">
          <div className="shimmer-text" />
        </div>
      </div>
      <div className="card-stats">
        <div className="stat-value shimmer-text" />
        <div className="stat-label shimmer-text" />
      </div>
    </div>
  );
};

export const TradeHistoryShimmer = () => {
  return (
    <div className="trade-history-container shimmer-card">
      <div className="trade-row shimmer" />
      <div className="trade-row shimmer" />
      <div className="trade-row shimmer" />
    </div>
  );
};
