import React, { useState, useEffect, useRef } from 'react';
import { fetchPerformanceData } from '../services/googleSheetsService';
import './styles/PerformanceData.css';
import { PerformanceCardShimmer } from './ui/ShimmerLoading';

const PerformanceData = () => {
  const [data, setData] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const retryCount = useRef(0);
  const maxRetries = 3;

  useEffect(() => {
    let isMounted = true;
    let timeoutId;

    const fetchData = async () => {
      try {
        const result = await fetchPerformanceData();
        
        if (!isMounted) return;

        if (result && Object.keys(result).length) {
          setData(result);
          setError(null);
          retryCount.current = 0;
        }
      } catch (err) {
        console.error('Error fetching performance data:', err);
        if (isMounted && !data) {
          retryCount.current += 1;
          if (retryCount.current < maxRetries) {
            timeoutId = setTimeout(fetchData, 2000);
          }
        }
      } finally {
        if (isMounted) {
          setIsLoading(false);
        }
      }
    };

    fetchData();
    const intervalId = setInterval(fetchData, 30000);

    return () => {
      isMounted = false;
      clearInterval(intervalId);
      if (timeoutId) clearTimeout(timeoutId);
    };
  }, []);

  if (isLoading) {
    return (
      <div className="performance-cards-container">
        {[1, 2, 3, 4].map((index) => (
          <PerformanceCardShimmer key={index} />
        ))}
      </div>
    );
  }

  if (error) {
    return <div className="error-message">{error}</div>;
  }

  const cards = [
    {
      title: 'This Week PNL',
      value: data?.thisWeek || '0.00',
      icon: 'fas fa-chart-line'
    },
    {
      title: 'Last Week PNL',
      value: data?.lastWeek || '0.00',
      icon: 'fas fa-history'
    },
    {
      title: 'Monthly PNL',
      value: data?.monthly || '0',
      icon: 'fas fa-calendar-alt'
    },
    {
      title: 'Yearly PNL',
      value: data?.yearly || '0',
      icon: 'fas fa-calendar-check'
    }
  ];

  return (
    <div className="performance-cards-container">
      {cards.map((card, index) => (
        <div key={index} className="performance-card">
          <div className="card-header">
            <div className="card-icon">
              <i className={card.icon}></i>
            </div>
            <div className="card-title">
              <h3>{card.title}</h3>
            </div>
          </div>
          <div className="card-stats">
            <span className={`stat-value ${parseFloat(card.value) >= 0 ? 'positive' : 'negative'}`}>
              {parseFloat(card.value) >= 0 ? '+' : ''}{card.value}%
            </span>
            <span className="stat-label">PROFIT</span>
          </div>
        </div>
      ))}
    </div>
  );
};

export default PerformanceData;
