import axios from 'axios';
import config from '../config/config';
import { API_URL } from './api';

axios.defaults.withCredentials = true;

const CACHE_DURATION = 30000; // 30 seconds cache
let cache = {
  performanceData: { data: null, timestamp: null },
  tradeHistory: { data: null, timestamp: null },
  pnlData: { data: null, timestamp: null }
};

const isCacheValid = (cacheKey) => {
  return cache[cacheKey].data && 
         cache[cacheKey].timestamp && 
         (Date.now() - cache[cacheKey].timestamp < CACHE_DURATION);
};

export const fetchPerformanceData = async () => {
  try {
    const token = localStorage.getItem('token');
    if (!token) {
      throw new Error('No authentication token found');
    }

    if (isCacheValid('performanceData')) {
      return cache.performanceData.data;
    }

    const response = await axios.get(`${API_URL}/api/performance-data`, {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      withCredentials: true,
      timeout: 15000,
      retry: 3,
      retryDelay: 1000
    });

    if (!response.data) {
      if (cache.performanceData.data) {
        return cache.performanceData.data;
      }
      throw new Error('No data received');
    }

    const formattedData = {
      thisWeek: Number(response.data.thisWeek || 0).toFixed(2),
      lastWeek: Number(response.data.lastWeek || 0).toFixed(2),
      monthly: Math.round(Number(response.data.monthly || 0)),
      yearly: Math.round(Number(response.data.yearly || 0))
    };

    cache.performanceData = {
      data: formattedData,
      timestamp: Date.now()
    };

    return formattedData;
  } catch (error) {
    console.error('Error fetching performance data:', error);
    if (cache.performanceData.data) {
      return cache.performanceData.data;
    }
    throw error;
  }
};

export const fetchTradeHistory = async (token) => {
  try {
    if (!token) {
      const storedToken = localStorage.getItem('token');
      if (!storedToken) {
        throw new Error('No authentication token found');
      }
      token = storedToken;
    }

    const axiosConfig = {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    };

    console.log('Fetching from:', `${config.API_URL}/api/trade-history`);
    const response = await axios.get(`${config.API_URL}/api/trade-history`, axiosConfig);
    console.log('Response:', response.data);

    return response.data;
  } catch (error) {
    console.error('Error details:', error.response || error);
    throw new Error(error.response?.data?.error || 'Failed to fetch trade history');
  }
};

const getAuthToken = () => {
  const token = localStorage.getItem('token');
  if (!token) {
    throw new Error('No authentication token found');
  }
  return token;
};

export const fetchPNLData = async () => {
  try {
    if (isCacheValid('pnlData')) {
      return cache.pnlData.data;
    }

    const token = getAuthToken();
    const axiosConfig = {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      withCredentials: true
    };

    const response = await axios.get(`${API_URL}/api/pnl-data`, axiosConfig);

    const data = response.data;
    
    // Ensure data is properly formatted
    const formattedData = Array.isArray(data) ? data.map(item => ({
      date: item.date,
      pnl: parseFloat(item.pnl || 0).toFixed(2)
    })) : [];

    cache.pnlData = {
      data: formattedData,
      timestamp: Date.now()
    };

    return formattedData;
  } catch (error) {
    if (error.response?.status === 429) {
      // Wait and retry once if rate limited
      await new Promise(resolve => setTimeout(resolve, 2000));
      return fetchPNLData();
    }
    throw error;
  }
};
