import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS } from 'chart.js/auto';
import { fetchPNLData } from '../services/googleSheetsService';
import './styles/PNLChart.css';
import LoadingSpinner from './LoadingSpinner';

function PNLChart() {
  const [chartData, setChartData] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    let isMounted = true;
    let retryCount = 0;
    const maxRetries = 3;

    const fetchData = async () => {
      try {
        if (retryCount >= maxRetries) {
          setError('Failed to load data after multiple attempts');
          return;
        }

        const data = await fetchPNLData();
        
        if (!isMounted) return;
        
        if (!data || data.length === 0) {
          throw new Error('No data available');
        }

        const processedData = data
          .filter(item => item.date && !isNaN(parseFloat(item.pnl)))
          .slice(-20);

        setChartData({
          labels: processedData.map(item => formatDate(item.date)),
          datasets: [{
            label: 'Daily PNL',
            data: processedData.map(item => parseFloat(item.pnl)),
            backgroundColor: processedData.map(item => 
              parseFloat(item.pnl) >= 0 ? 'rgba(14, 203, 129, 0.8)' : 'rgba(246, 70, 93, 0.8)'
            ),
            borderColor: processedData.map(item => 
              parseFloat(item.pnl) >= 0 ? 'rgba(14, 203, 129, 1)' : 'rgba(246, 70, 93, 1)'
            ),
            borderWidth: 1,
            borderRadius: 4,
            barThickness: 8,
          }]
        });
        setError(null);
      } catch (error) {
        console.error('Error fetching data:', error);
        retryCount++;
        if (isMounted) {
          await new Promise(resolve => setTimeout(resolve, 2000));
          fetchData();
        }
      } finally {
        if (isMounted) {
          setIsLoading(false);
        }
      }
    };

    fetchData();
    const intervalId = setInterval(fetchData, 10000);

    return () => {
      isMounted = false;
      clearInterval(intervalId);
    };
  }, []);

  const formatDate = (date) => {
    if (!date) return "-";
    try {
      let dateObj;
      if (typeof date === 'number') {
        dateObj = new Date((date - 25569) * 86400 * 1000);
      } else {
        dateObj = new Date(date);
      }
      
      if (isNaN(dateObj.getTime())) {
        return "-";
      }
      
      // Get day and month in desired format
      const day = dateObj.getDate();
      const month = dateObj.toLocaleString('en-US', { month: 'short' });
      
      // Return array with day and month on separate lines
      return [`${day}`, `${month}`];
    } catch (error) {
      console.error('Error formatting date:', error);
      return ["-", ""];
    }
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    layout: {
      padding: {
        top: 20,
        right: 20,
        bottom: 0,
        left: 0
      }
    },
    scales: {
      y: {
        grid: {
          display: false,
          drawBorder: false,
        },
        ticks: {
          color: '#808080',
          padding: 0,
          align: 'end',
          callback: function(value) {
            const isMobile = window.innerWidth <= 768;
            const formattedValue = value < 0 ? value.toString().replace('-', '- ') : value;
            return isMobile ? `  ${formattedValue}%` : formattedValue + '%';
          },
          font: {
            size: ({ chart }) => (chart.width <= 768 ? 10 : 11),
            weight: '400'
          },
          maxTicksLimit: window.innerWidth <= 768 ? 4 : 6
        },
        position: 'left',
        offset: false,
        beginAtZero: true
      },
      x: {
        grid: {
          display: false,
          drawBorder: false,
        },
        ticks: {
          color: '#808080',
          maxRotation: 0,
          padding: 5,
          font: {
            size: 11
          }
        }
      }
    },
    plugins: {
      legend: {
        display: false
      },
      tooltip: {
        enabled: true,
        mode: 'index',
        intersect: false,
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        titleColor: '#808080',
        bodyColor: '#808080',
        padding: 12,
        cornerRadius: 4,
        callbacks: {
          title: function(tooltipItems) {
            const label = tooltipItems[0].label;
            if (Array.isArray(label)) {
              return `${label[0]} ${label[1]}`; // Combine day and month for tooltip
            }
            return label;
          },
          label: function(context) {
            return `PNL: ${context.parsed.y}%`;
          }
        }
      }
    },
    barPercentage: 0.6,
    categoryPercentage: 0.7,
    animation: {
      duration: 750,
      easing: 'easeInOutQuart'
    },
    hover: {
      mode: 'index',
      intersect: false
    }
  };

  if (isLoading) {
    return (
      <div className="pnl-chart-container">
        <div className="chart-loading">
          <LoadingSpinner />
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="pnl-chart-container">
        <div className="chart-error">{error}</div>
      </div>
    );
  }

  if (!chartData && !error) {
    return (
      <div className="performance-cards-container">
        <div className="performance-card full-width-chart">
          <LoadingSpinner />
        </div>
      </div>
    );
  }

  return (
    <div className="pnl-chart-container">
      <div className="chart-content">
        <Bar data={chartData} options={chartOptions} />
      </div>
    </div>
  );
}

export default PNLChart; 